/* styles.css */

@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600;700&display=swap');

 /* CSS RESET */
 *,
 *::before,
 *::after {
   box-sizing: border-box;
 }

 * {
   margin: 0;
 }

 body {
   line-height: 1.5;
   -webkit-font-smoothing: antialiased;
 }

 img,
 picture,
 video,
 canvas,
 svg {
   display: block;
   max-width: 100%;
 }

 input,
 button,
 textarea,
 select {
   font: inherit;
 }

 p,
 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
   overflow-wrap: break-word;
 }

 /* APP */

 body {
   background-color: #2a6812;
   background-image: url("https://www.transparenttextures.com/patterns/cartographer.png");
   color: white;
   font-family: 'Rubik', sans-serif;
 }

 /* container */

 .container {
   max-width: 1200px;
   margin: 0 auto;
 }

 /* todos list */

 .title {
   text-align: center;
   font-size: 3rem;
   padding: 3rem 0 1rem;
 }
 h2{
    text-align: center;
    font-size: 1rem;
    padding: 1rem 0 1rem;
 }

 .todos {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 1rem;
 }

 .todo {
   display: flex;
   justify-content: space-between;
   padding: 1rem 1.25rem;

   background-color: rgba(255, 255, 255, 0.1);
   width: auto;
   font-size: 1.4rem;
   border-radius: 1rem;
   transition: all 0.2s;

   &:hover {
    background-color: rgba(255, 255, 255, 0.2);
   }
 }

 ul{
        display: flex;
        justify-content: space-between;
        padding: 1rem 1rem;
        background-color: rgba(255, 255, 255, 0.1);
        width: auto;
        font-size: 1.4rem;
        border-radius: 1rem;
        transition: all 0.2s;
        list-style-type: none;
        li a{
            font-size: 1rem;
            color:aquamarine;
            ::active {
                color: white;
               }
        }
 }

 .mutations {
   display: flex;
   gap: 0.75rem;
 }

 .todo__status,
 .todo__delete, .todo__save, .todo__edit {
   border: none;
   padding: 0;
   line-height: 1;
   cursor: pointer;
   transition: all 0.2s;
   background: none;
   color: white;
   font-size: 1.6rem;

   &:hover {
     transform: scale(1.15);
   }
 }

 .todo__status {
  font-size: 1.75rem;
 }


 /* form */
 .form {
   display: flex;
   gap: 0.5rem;
   width: 50%;
   margin: 1rem auto;
 }

 .form__input {
   flex: 1;
   padding: 0.5rem 0.75rem;
   border-radius: 1rem;
   border: none;
   outline: none;
   background-color: rgb(240, 235, 163);

   &:focus,
   &:hover {
    background-color: rgba(255, 255, 255, 0.85);
   }
 }
 .submit-btn{
    flex: 1;
    padding: 0.5rem 0.75rem;
    border-radius: 1rem;
    border: none;
    outline: none;
    background-color: rgb(163, 194, 240); 
 }

 .form__button {
  border: none;
  background-color: rgba(113, 0, 212, 0.7);
  cursor: pointer;
  color: white;
  border-radius: 0.25rem;
  transition: all 0.2s;

  &:hover {
    background-color: rgba(95, 0, 179, 0.5) ;
  }
 }

 
/* responsiveness */

@media (max-width: 420px) {
    .title {
      font-size: 2.5rem;
    }
  
    .form {
      width: 95%;
    }
  
    .todo {
      width: 95%;
      font-size: 1.2rem;
    }
    ul{
        width:95;
        font-size: 1rem;
    }
   }